<template>
  <div>
    <div class="row">
      <div class="col-12 bg-black h-10 shadow-lg rounded-lg">
        <button
          type="button"
          @click="$router.push('/')"
          class="btn btn-sm btn-danger absolute"
          style="top: 5px; left: 40px"
        >
          <span class="typcn typcn-arrow-left"></span>
        </button>
        <p class="text-center p-2 font-bold text-white">SUKU BUNGA</p>
      </div>
      <div class="col-12 p-3">
        <p class="text-center text-2xl font-bold">INPUT SUKU BUNGA</p>
        <div class="p-3">
            <div class="sm-form ">
                <label for="title">Title</label>
                <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
            </div>
            <div class="sm-form ">
                <label for="sub_kiri">Sub_kiri</label>
                <input type="text" id="sub_kiri" name="sub_kiri" class="form-control form-control-sm" placeholder="sub_kiri" v-model="vdata.sub_kiri" >
            </div>
            <div class="sm-form ">
                <label for="sub_kanan">Sub_kanan</label>
                <input type="text" id="sub_kanan" name="sub_kanan" class="form-control form-control-sm" placeholder="sub_kanan" v-model="vdata.sub_kanan" >
            </div>
            <hr class="style13" />
            <button type="submit" @click="add()" class="btn btn-sm btn-primary">Tambah +</button>
            <button type="submit" @click="update(vdata.id)" class="btn btn-sm btn-success">Update <span class="typcn typcn-pencil"></span></button>
            <button type="submit" @click="hapus(vdata.id)" class="btn btn-sm btn-danger">Delete X</button>
            <hr class="style13">
            <div class="sm-form ">
                <label for="sub_1">sub_1</label>
                <input type="text" id="sub_1" name="sub_1" class="form-control form-control-sm" placeholder="sub_1" v-model="sub_1" >
            </div>
            <div class="sm-form ">
                <label for="sub_2">sub_2</label>
                <input type="text" id="sub_2" name="sub_2" class="form-control form-control-sm" placeholder="sub_2" v-model="sub_2" >
            </div>
            <button type="button" @click="vdata.isi.push({sub_1:JSON.parse(JSON.stringify(sub_1)),sub_2:JSON.parse(JSON.stringify(sub_2))})" class="btn btn-sm btn-info  ">ADD</button>
            <button type="button" @click="vdata.isi.splice(indexnya,1,{sub_1:JSON.parse(JSON.stringify(sub_1)),sub_2:JSON.parse(JSON.stringify(sub_2))})" class="btn btn-sm btn-success">Change</button>
            <br>
            <div class="p-3 mt-1 shadow-lg rounded-lg " @click="indexnya=index;sub_1=vdata.isi[index].sub_1;sub_2=vdata.isi[index].sub_2;" v-for="(item, index) in vdata.isi" :key="index+'isi'">{{item.sub_1}}&nbsp;&nbsp;&nbsp; / &nbsp;&nbsp;&nbsp;{{item.sub_2}}
                <span class="float-right bg-black text-white p-2" @click="vdata.isi.splice(index,1);">X</span>
            </div>
            <ul>
                <li></li>
            </ul>
        </div>
      </div>
      <div class="offset-1 col-11">
        <div
          class="table-responsive-sm font-times p-3"
          style="box-shadow: 1px 4px 6px black; overflow: scroll"
          v-dragscroll
        >
          <table
            class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
            style="
              overflow-x: scroll;
              overflow-y: hidden;
              height: 80px;
              white-space: nowrap;
            "
          >
            <thead class="mdb-color bg-black">
              <tr class="text-white">
                <th style="color:white;" class="">title</th>
                <th style="color:white;" class="">sub_kiri</th>
                <th style="color:white;">sub_kanan</th>
              </tr>
            </thead>
            <tbody>
              <tr @click="ambil(item)" v-for="(item, index) in datanya" :key="index+'datanya'">
                <td>{{item.title}}</td>
                <td>{{item.sub_kiri}}</td>
                <td>{{item.sub_kanan}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
let db = firebase.firestore();
export default {
  data() {
    return {
      vdata: {
          isi:[]
      },
      indexnya:0,
      sub_1: "",
      sub_2: "",
      datanya:[]
    };
  },
  methods: {
    ambil(item){
        this.vdata = JSON.parse(JSON.stringify(item))
        console.log(item)
        this.$forceUpdate()
    },
    add() {
        console.log(this.vdata)
        db.collection('tv/btn/sukubunga').doc().set(this.vdata,{merge:true}).then(res=>{
            console.log("BERHASIL INSERT")
        }).catch(err=>{
            console.log("err",err)
        })
    },
    update(id) {
        if(id){
            if(confirm("Yakin Edit ?")){
                db.collection('tv/btn/sukubunga').doc(id).set(this.vdata,{merge:true}).then(res=>{
                    console.log("UPDATE BERHASIL")
                }).catch(err=>{
                    console.log("err",err)
                })
            }
        }else{
            alert('KLIK SALAH SATU DATA!')
        }
        
    },
    hapus(id) {
        if(id){
            if(confirm("YAKIN DIHAPUS ?")){
                db.collection('tv/btn/sukubunga').doc(id).delete().then(res=>{
                    console.log("DELETE BERHASIL")
                }).catch(err=>{
                    console.log("err",err)
                })
            }
        }else{
            alert('KLIK SALAH SATU DATA!')
        }
        
    },
    getData(){
        db.collection('tv/btn/sukubunga').onSnapshot(res=>{
            let data = res.docs.map(e=>{
                return {
                    id:e.id,
                    ...e.data()
                }
                })
            console.log(data)
            this.datanya=data
        })
    },
  },
  mounted() {
      this.getData()
  },
};
</script>